            <h1 class="modern-title">üñºÔ∏è ÂõæÁâáÊãºÊé•Âô®</h1>
            <p class="page-description">Â∞ÜÂ§öÂº†ÂõæÁâáÊ®™ÂêëÊàñÁ∫µÂêëÊãºÊé•Êàê‰∏ÄÂº†ÂõæÁâáÔºåÊîØÊåÅÈó¥Ë∑ùË∞ÉÊï¥ÂíåÂØπÈΩêÊñπÂºè</p>
          </div>

    <!-- ÁßªÂä®Á´ØÂèãÂ•ΩÁöÑÊìç‰ΩúÂå∫Âüü -->
    <div class="operation-section">
      <!-- ‰∏ä‰º†Âå∫Âüü -->
      <div class="upload-section">
        <el-upload
          class="upload-dragger"
          drag
          action=""
          :auto-upload="false"
          :on-change="handleFileChange"
          :show-file-list="false"
          multiple
          accept="image/*"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">Â∞ÜÂõæÁâáÊãñÂà∞Ê≠§Â§ÑÔºåÊàñ<em>ÁÇπÂáª‰∏ä‰º†</em></div>
          <div class="el-upload__tip" slot="tip">ÊîØÊåÅ JPG„ÄÅPNG„ÄÅGIF Ê†ºÂºèÔºåÂèØÈÄâÊã©Â§öÂº†ÂõæÁâá</div>
        </el-upload>
      </div>

      <!-- ËÆæÁΩÆÂå∫Âüü - ÁßªÂä®Á´ØÂûÇÁõ¥Â∏ÉÂ±Ä -->
      <div class="settings-section">
        <div class="setting-row">
          <label>ÊãºÊé•ÊñπÂêëÔºö</label>
          <el-radio-group v-model="mergeDirection" size="small">
            <el-radio label="horizontal">Ê®™ÂêëÊãºÊé•</el-radio>
            <el-radio label="vertical">Á∫µÂêëÊãºÊé•</el-radio>
          </el-radio-group>
        </div>

        <div class="setting-row">
          <label>ÂØπÈΩêÊñπÂºèÔºö</label>
          <el-radio-group v-model="alignMode" size="small">
            <el-radio label="start">È°∂ÈÉ®/Â∑¶‰æßÂØπÈΩê</el-radio>
            <el-radio label="center">Â±Ö‰∏≠ÂØπÈΩê</el-radio>
            <el-radio label="end">Â∫ïÈÉ®/Âè≥‰æßÂØπÈΩê</el-radio>
          </el-radio-group>
        </div>

        <div class="setting-row">
          <label>ÂõæÁâáÈó¥Ë∑ùÔºö</label>
          <el-slider
            v-model="spacing"
            :min="0"
            :max="50"
            :step="1"
            show-input
            input-size="small"
            style="width: 200px;"
          ></el-slider>
          <span class="unit">px</span>
        </div>

        <div class="setting-row">
          <label>ËÉåÊôØÈ¢úËâ≤Ôºö</label>
          <el-color-picker v-model="backgroundColor" size="small"></el-color-picker>
        </div>
      </div>

      <!-- Êìç‰ΩúÊåâÈíÆ -->
      <div class="action-buttons">
        <el-button type="primary" @click="mergeImages" :disabled="images.length < 2" :loading="processing">
          <i class="el-icon-picture"></i> ÂºÄÂßãÊãºÊé•
        </el-button>
        <el-button @click="clearImages" :disabled="images.length === 0">
          <i class="el-icon-delete"></i> Ê∏ÖÁ©∫ÂõæÁâá
        </el-button>
        <el-button v-if="mergedImageUrl" type="success" @click="downloadMergedImage">
          <i class="el-icon-download"></i> ‰∏ãËΩΩÁªìÊûú
        </el-button>
      </div>
    </div>

    <!-- ÂõæÁâáÈ¢ÑËßàÂå∫Âüü - ÁßªÂä®Á´ØÂèãÂ•Ω -->
    <div class="preview-section" v-if="images.length > 0">
      <h3>üì∑ Â∑≤‰∏ä‰º†ÂõæÁâá ({{ images.length }}Âº†)</h3>
      <div class="image-list">
        <div 
          v-for="(image, index) in images" 
          :key="index" 
          class="image-item"
        >
          <img :src="image.url" :alt="`ÂõæÁâá${index + 1}`" />
          <div class="image-info">
            <p>{{ image.name }}</p>
            <p>{{ image.width }}√ó{{ image.height }}</p>
          </div>
          <el-button 
            type="danger" 
            size="mini" 
            icon="el-icon-delete" 
            circle 
            @click="removeImage(index)"
            class="remove-btn"
          ></el-button>
        </div>
      </div>
    </div>

    <!-- ÁªìÊûúÈ¢ÑËßàÂå∫Âüü -->
    <div class="result-section" v-if="mergedImageUrl">
      <h3>‚ú® ÊãºÊé•ÁªìÊûú</h3>
      <div class="result-preview">
        <img :src="mergedImageUrl" alt="ÊãºÊé•ÁªìÊûú" class="merged-image" />
        <div class="result-info">
          <p><strong>Â∞∫ÂØ∏Ôºö</strong>{{ mergedWidth }}√ó{{ mergedHeight }}</p>
          <p><strong>Êñá‰ª∂Â§ßÂ∞èÔºö</strong>{{ formatFileSize(mergedFileSize) }}</p>
        </div>
      </div>
    </div>

          <!-- Â§ÑÁêÜËøõÂ∫¶ -->
          <div v-if="processing" class="processing-overlay">
            <div class="processing-content">
              <el-progress :percentage="progress" :stroke-width="8"></el-progress>
              <p>{{ progressText }}</p>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'ImageMergerView',
  metaInfo: {
    // Ê†áÈ¢òÔºöÁ™ÅÂá∫Ê†∏ÂøÉÂäüËÉΩÔºàÊãºÊé•„ÄÅÂêàÊàêÈïøÂõæÔºâ„ÄÅÊñπÂêëÊîØÊåÅÂíåËá™ÂÆö‰πâÂäüËÉΩ
    title: 'ÂõæÁâáÊãºÊé•Â∑•ÂÖ∑ - ÂÖçË¥πÂú®Á∫øÊ®™Âêë/Á∫µÂêëÈïøÂõæÂà∂‰Ωú„ÄÅÂ§öÂõæÂêàÂπ∂‰∏éÈó¥Ë∑ùË∞ÉÊï¥',

    meta: [
      // ÊèèËø∞ÔºöËØ¶ÁªÜËØ¥ÊòéÂ∑•ÂÖ∑ÁöÑÊãºÊé•ÊñπÂêë„ÄÅÈ´òÁ∫ßÊéßÂà∂ÈÄâÈ°πÔºàÈó¥Ë∑ù„ÄÅÂØπÈΩêÔºâÔºå‰ª•ÂèäÂ∫îÁî®ÁöÑÂú∫ÊôØ
      {
        name: 'description',
        content: 'ÂÖçË¥πÂú®Á∫øÂõæÁâáÊãºÊé•Â∑•ÂÖ∑„ÄÇÊîØÊåÅÂ∞ÜÂ§öÂº†ÂõæÁâáÂø´ÈÄüÊ®™ÂêëÊàñÁ∫µÂêëÂêàÂπ∂‰∏∫‰∏ÄÂº†ÈïøÂõæ„ÄÇÊèê‰æõËá™ÂÆö‰πâÂõæÁâáÈó¥Ë∑ù„ÄÅÂØπÈΩêÊñπÂºèÁ≠âÈ´òÁ∫ßËÆæÁΩÆÔºåËΩªÊùæÂà∂‰ΩúÂØπÊØîÂõæ„ÄÅÁ§æ‰∫§Â™í‰ΩìÈïøÊñáÂàÜ‰∫´ÂõæÁ≠â„ÄÇ'
      },

      // ÂÖ≥ÈîÆËØçÔºöË¶ÜÁõñÊ†∏ÂøÉÂä®‰ΩúÔºàÊãºÊé•„ÄÅÂêàÂπ∂„ÄÅÂêàÊàêÔºâ„ÄÅËæìÂá∫Á±ªÂûãÔºàÈïøÂõæÔºâÂíåÊñπÂêë
      {
        name: 'keywords',
        content: 'ÂõæÁâáÊãºÊé•Âô®, Âú®Á∫øÂõæÁâáÂêàÂπ∂, Ê®™ÂêëÊãºÊé•, Á∫µÂêëÊãºÊé•, ÈïøÂõæÂà∂‰Ωú, Â§öÂõæÂêàÊàê, ÂõæÁâáÈó¥Ë∑ùË∞ÉÊï¥, ÂÖçË¥πÂõæÁâáÂ∑•ÂÖ∑'
      }
    ]
  },
  data() {
    return {
      images: [],
      mergeDirection: 'horizontal', // horizontal | vertical
      alignMode: 'center', // start | center | end
      spacing: 10,
      backgroundColor: '#FFFFFF',
      processing: false,
      progress: 0,
      progressText: '',
      mergedImageUrl: null,
      mergedWidth: 0,
      mergedHeight: 0,
      mergedFileSize: 0
    }
  },
  methods: {
    // Â§ÑÁêÜÊñá‰ª∂ÈÄâÊã©
    async handleFileChange(file) {
      if (!file.raw) return
      
      // Ê£ÄÊü•Êñá‰ª∂Á±ªÂûã
      if (!file.raw.type.startsWith('image/')) {
        this.$message.error('ËØ∑ÈÄâÊã©ÂõæÁâáÊñá‰ª∂ÔºÅ')
        return
      }

      // Ê£ÄÊü•Êñá‰ª∂Â§ßÂ∞è (10MB)
      if (file.raw.size > 10 * 1024 * 1024) {
        this.$message.error('ÂõæÁâáÊñá‰ª∂‰∏çËÉΩË∂ÖËøá10MBÔºÅ')
        return
      }

      try {
        const imageData = await this.loadImage(file.raw)
        this.images.push(imageData)
        this.$message.success(`Â∑≤Ê∑ªÂä†ÂõæÁâáÔºö${file.name}`)
      } catch (error) {
        console.error('Âä†ËΩΩÂõæÁâáÂ§±Ë¥•:', error)
        this.$message.error('ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•Êñá‰ª∂Ê†ºÂºèÔºÅ')
      }
    },

    // Âä†ËΩΩÂõæÁâá
    loadImage(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.onload = (e) => {
          const img = new Image()
          img.onload = () => {
            resolve({
              name: file.name,
              url: e.target.result,
              width: img.width,
              height: img.height,
              file: file,
              element: img
            })
          }
          img.onerror = reject
          img.src = e.target.result
        }
        reader.onerror = reject
        reader.readAsDataURL(file)
      })
    },

    // ÁßªÈô§ÂõæÁâá
    removeImage(index) {
      this.images.splice(index, 1)
      this.$message.info('Â∑≤ÁßªÈô§ÂõæÁâá')
    },

    // Ê∏ÖÁ©∫ÊâÄÊúâÂõæÁâá
    clearImages() {
      this.images = []
      this.mergedImageUrl = null
      this.$message.info('Â∑≤Ê∏ÖÁ©∫ÊâÄÊúâÂõæÁâá')
    },

    // ÊãºÊé•ÂõæÁâá
    async mergeImages() {
      if (this.images.length < 2) {
        this.$message.warning('Ëá≥Â∞ëÈúÄË¶Å2Âº†ÂõæÁâáÊâçËÉΩÊãºÊé•ÔºÅ')
        return
      }

      this.processing = true
      this.progress = 0
      this.progressText = 'Ê≠£Âú®ËÆ°ÁÆóÊãºÊé•Â∞∫ÂØ∏...'

      try {
        // ËÆ°ÁÆóÊãºÊé•ÂêéÁöÑÂ∞∫ÂØ∏
        const { totalWidth, totalHeight } = this.calculateMergedSize()
        
        this.progress = 20
        this.progressText = 'Ê≠£Âú®ÂàõÂª∫ÁîªÂ∏É...'

        // ÂàõÂª∫ÁîªÂ∏É
        const canvas = document.createElement('canvas')
        canvas.width = totalWidth
        canvas.height = totalHeight
        const ctx = canvas.getContext('2d')

        // ËÆæÁΩÆËÉåÊôØËâ≤
        ctx.fillStyle = this.backgroundColor
        ctx.fillRect(0, 0, totalWidth, totalHeight)

        this.progress = 40
        this.progressText = 'Ê≠£Âú®ÊãºÊé•ÂõæÁâá...'

        // ÊãºÊé•ÂõæÁâá
        await this.drawImagesOnCanvas(ctx, totalWidth, totalHeight)

        this.progress = 80
        this.progressText = 'Ê≠£Âú®ÁîüÊàêÁªìÊûú...'

        // ÁîüÊàêÁªìÊûú
        const dataUrl = canvas.toDataURL('image/png', 0.9)
        this.mergedImageUrl = dataUrl
        this.mergedWidth = totalWidth
        this.mergedHeight = totalHeight
        
        // ËÆ°ÁÆóÊñá‰ª∂Â§ßÂ∞è
        const base64Data = dataUrl.split(',')[1]
        this.mergedFileSize = Math.round((base64Data.length * 3) / 4)

        this.progress = 100
        this.progressText = 'ÊãºÊé•ÂÆåÊàêÔºÅ'

        setTimeout(() => {
          this.processing = false
          this.$message.success('ÂõæÁâáÊãºÊé•ÂÆåÊàêÔºÅ')
        }, 500)

      } catch (error) {
        console.error('ÊãºÊé•Â§±Ë¥•:', error)
        this.processing = false
        this.$message.error('ÂõæÁâáÊãºÊé•Â§±Ë¥•Ôºö' + error.message)
      }
    },

    // ËÆ°ÁÆóÊãºÊé•ÂêéÁöÑÂ∞∫ÂØ∏
    calculateMergedSize() {
      let totalWidth = 0
      let totalHeight = 0
      let maxWidth = 0
      let maxHeight = 0

      this.images.forEach(img => {
        maxWidth = Math.max(maxWidth, img.width)
        maxHeight = Math.max(maxHeight, img.height)
      })

      if (this.mergeDirection === 'horizontal') {
        // Ê®™ÂêëÊãºÊé•
        totalWidth = this.images.reduce((sum, img) => sum + img.width, 0) + (this.images.length - 1) * this.spacing
        totalHeight = maxHeight
      } else {
        // Á∫µÂêëÊãºÊé•
        totalWidth = maxWidth
        totalHeight = this.images.reduce((sum, img) => sum + img.height, 0) + (this.images.length - 1) * this.spacing
      }

      return { totalWidth, totalHeight, maxWidth, maxHeight }
    },

    // Âú®ÁîªÂ∏É‰∏äÁªòÂà∂ÂõæÁâá
    async drawImagesOnCanvas(ctx, totalWidth, totalHeight) {
      let currentX = 0
      let currentY = 0

      for (let i = 0; i < this.images.length; i++) {
        const img = this.images[i]
        let drawX = currentX
        let drawY = currentY

        if (this.mergeDirection === 'horizontal') {
          // Ê®™ÂêëÊãºÊé•Êó∂ÁöÑÂûÇÁõ¥ÂØπÈΩê
          if (this.alignMode === 'center') {
            drawY = (totalHeight - img.height) / 2
          } else if (this.alignMode === 'end') {
            drawY = totalHeight - img.height
          }
        } else {
          // Á∫µÂêëÊãºÊé•Êó∂ÁöÑÊ∞¥Âπ≥ÂØπÈΩê
          if (this.alignMode === 'center') {
            drawX = (totalWidth - img.width) / 2
          } else if (this.alignMode === 'end') {
            drawX = totalWidth - img.width
          }
        }

        // ÁªòÂà∂ÂõæÁâá
        ctx.drawImage(img.element, drawX, drawY, img.width, img.height)

        // Êõ¥Êñ∞‰ΩçÁΩÆ
        if (this.mergeDirection === 'horizontal') {
          currentX += img.width + this.spacing
        } else {
          currentY += img.height + this.spacing
        }

        // Êõ¥Êñ∞ËøõÂ∫¶
        this.progress = 40 + (i + 1) / this.images.length * 40
      }
    },

    // ‰∏ãËΩΩÊãºÊé•ÁªìÊûú
    downloadMergedImage() {
      if (!this.mergedImageUrl) return

      const link = document.createElement('a')
      link.download = `merged_image_${Date.now()}.png`
      link.href = this.mergedImageUrl
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      
      this.$message.success('ÂºÄÂßã‰∏ãËΩΩÊãºÊé•ÁªìÊûúÔºÅ')
    },

    // Ê†ºÂºèÂåñÊñá‰ª∂Â§ßÂ∞è
    formatFileSize(bytes) {
      if (bytes === 0) return '0 B'
      const k = 1024
      const sizes = ['B', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    }
  }
}
</script>

